body{
  overflow-y:scroll;
  overflow-x:hidden;
}
:root,body,html{
  background-color:#f5f5f5;
  box-sizing:border-box;

}

div ,h1,h2,h3,h4,h5,header,html,img,label,li,nav,p,small,span,ul{
  margin:0;
  padding:0;
  border:0;
  outline:none;
  font-size:100%;
  vertical-align: baseline;
  background-color:transparent ;
}
body{
  font-family:Arial, Helvetica, sans-serif;
}
@-webkit-keyframes fadein{
  from {
    opacity:0;
  }
  to {
    opacity:1;
  }
}
@keyframes fadein{
  from {
    opacity:0;
  }
  to {
    opacity:1;
  }
}